<template>
  <section class="main-view">
    <div class="container-scroller" v-if="loadSuccess">
      <Header/>
      <div class="container-fluid page-body-wrapper">
        <Sidebar :key="$store.state.scheme_num"/>
        <div class="main-panel">
          <div class="content-wrapper">
            <router-view :key="$route.params.id + $route.name"></router-view>
<!--            router-viewkey$route.params.id,scheme/:id/overview/:daysoverviewbug;route.namedepositwithdraw-->
          </div> <!-- content wrapper ends -->
          <Footer/>
        </div> <!-- main panel ends -->
      </div> <!-- page-body-wrapper ends -->
    </div>
  </section>
</template>

<script>
  import Header from "./partials/Header";
  import Sidebar from "./partials/Sidebar";
  import Footer from "./partials/Footer";
  import getIdentity from "../store/userInfo";
  import axios from "../plugins/axios";
  import store from "../store";
  import {convert_time} from "../utils/time";
  import moment from "moment";
  export default {
    name: "layout",
    async created() { //mountedmountedcreated
      await this.getCoinInfo();
      await this.getIdentity();
    },
    data(){
      return {
        loadSuccess:false,
      }
    },
    components: {
      Header,
      Sidebar,
      Footer
    },
    watch:{
      $route:async function (n, o) {
        if (n.name != o.name || o.params.id != n.params.id) {
          await this.getIdentity();
        }
      }
    },
    methods: {
      toggleSidebar: () => {
        document.querySelector('body').classList.toggle('sidebar-icon-only');
      },
      toggleMobileSidebar: () => {
        document.querySelector('#sidebar').classList.toggle('active');
      },
      getIdentity: async function () {
        let userInfo = await getIdentity();
        if (userInfo.role == "guest") {
          this.$message.info({message:'Please log in',center:true});
          await this.$router.push("/login");
        }
      },
      getCoinInfo: async function () {
        let getCoinInfo = await axios.get("getCoinInfo");
        convert_time(getCoinInfo);
        store.commit("setCoinInfo",getCoinInfo.data);
        // console.log(this.$store.state.coinInfo);
        this.loadSuccess = true; //coin
      },
      logout: async function () {
        await this.$axios.get("logout");
        await this.$router.push("/login");
      },
    }
  }
</script>



