import axios from "axios";
import { Message } from "element-ui";
import router from '../router'
// import qs from 'qs';
// import store from '@/store';
// NProgress, JSCSS
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import store from '../store'

// axios
const service = axios.create({
    // apibase_url
    // baseURL: process.env.VUE_APP_URL, // -
    baseURL: process.env.VUE_APP_BACKEND_URL,
    withCredentials: true,
    timeout: 999000 // 
});
// request 
service.interceptors.request.use(
    config => {
        if(config.url.indexOf("wait") == -1){ // waitprogress bar
            NProgress.start();
        }

        //djangopost
        if(config.method == "post")
        {
            let param = new URLSearchParams();
            for (let key in config.data){
                param.append(key, config.data[key]);
            }
            config.data = param;
        }
        // //header
        // config.headers["Content-Type"] = "application/json;charset=UTF-8";
        //
        // //  get 
        // if (config.method === 'get' && config.data) {
        //     config.url = `${config.url}?${qs.stringify(config.data, { indices: false })}`
        //     config.headers["Content-Type"] = "application/x-www-form-urlencoded";
        //
        // }
        // // token
        // if (store.getters.token) {
        //     // headertoken
        //     config.headers["Authorization"] = store.getters.token;
        // }
        return config;
    },
    error => {
        return Promise.reject(error);
    }
);
// response 
service.interceptors.response.use(
    response => {

        if(response.request.responseURL.indexOf("wait") == -1){
            NProgress.done();
        }

        if (response.data.status != 200 && response.data.status != 210 && 'msg' in response.data) {
            Message({
                message: response.data.msg,
                type: 'error',
                center: true
            });
            // if(response.data.status == 302){
            //     store.commit("setBackRef",window.location.pathname);
            //     router.push("/login"); //When not logged in, return to home page.
            // }
            return false;
        } else if(response.data.status == 210){
            Message({
                message: response.data.msg,
                type: 'info',
                center: true
            });
            return false;
        } else if('msg' in response.data){
            Message({
                message: response.data.msg,
                type: 'success',
                center: true
            });
        }
        return response.data;
    },
    error => {
        NProgress.done();
        console.log(error);
        Message({
            message: "Server Error!",
            type: "error",
            duration: 5 * 1000
        });
        return Promise.reject(error);
    }
);

export default service;
