<style lang="scss">
$gradients: (
    0:linear-gradient(to right, #32a3ff, #047edf 99%),
    1:linear-gradient(to right, #da8cff, #9a55ff),
    2:linear-gradient(to right, #84d9d2, #07cdae),
    3:linear-gradient(to right, #ffbf96, #fe7096),
    4:linear-gradient(to right, #f6e384, #ffd500)
);
$non-gradients: (
    0: #198ae3,
    1: #b66dff,
    2: #1bcfb4,
    3: #fe7c96,
    4: #fed713,
);
.modal-header .close {
  margin: -20px -20px -25px auto!important;
}
//@media (max-width:769px){
//font-size: 1.5rem;
//}
//
/**/
@media (min-width: 768px) {
  .invest-table-div {
    .b-table-sticky-header {
      max-height: 33.5rem !important;
      overflow: auto;
      //max-height: 100%;
    }
  }
  .invest-status {
    display: flex;
    justify-content: space-between; // 
    span {
      font-family: ubuntu-regular;
      text-align: center;
    }
  }
  .invest-table-wait-div {
    .b-table-sticky-header {
      max-height: 24.7rem !important;
      overflow: auto;
      //max-height: 100%;
    }
  }
  .invest-wait-select{
    width:70px;
    min-width: 70px;
  }
  .check-coin {
    //margin-top: 0.8rem !important;
    margin-top: 0!important;
    margin-bottom: 0!important;
    //position: relative;
    //.check-hide-inner{
    //  position: absolute;
    //  right:0.5rem;
    //}
    //position:

    .form-check-label {
      //float:right;
      font-family: ubuntu-regular;
      font-size: 1rem !important;

      .input-helper {

        &:before {
          width: 20px !important;
          height: 20px !important;
          //margin-top:20px;
          font-size: 1rem;

        }
        &:before,&:after{

          top:-0.7rem!important;
          left:0.85rem!important;
        }
        &:after {
          //line-height: 20px!important;
          margin-top: 1px;
          margin-left: 0px;
        }
      }
    }
  }
  table#invest-wait-table {

    .invest-wait-symbol {
      text-align: left !important;
      font-size: 1rem;

      img {
        width: 30px;
        height: 30px;
      }

      .percentage {
        font-size: 0.8rem;
      }
    }

    th:nth-child(2) {
      text-align: left !important;
    }
  }
  table#invest-table {
    font-family: 'ubuntu-regular';

    thead {
      th {
        z-index: 1003;

      }
      .b-table-sticky-column{
        z-index:1004;
      }
    }

    tbody {
      .b-table-sticky-column {
        z-index: 1002;
      }

      tr:nth-of-type(odd) {

        background-color: rgb(252, 251, 253) !important;

        .b-table-sticky-column {
          //background-color: transparent;
